h1, h2, h3, h4, h5, h6 {
	color: #170404;
}
a {
	outline: none;
}
a[rel~="tag"] {
	color: #fff;
	transition: 0.3s ease-in-out;
	&:hover {
		color: #ccc;
		transition: 0.3s ease-in-out;
	}
}
p {
	color: #170404;
}
.secondary-heading {
	color: #bf0531;
	letter-spacing: 0.05em;
	font-weight: 500;
}
.tertiary-heading {
	color: #fff;
	letter-spacing: 0.05em;
	font-weight: 500;
}
.sub-header {
	min-height: 44px;
	width: 100%;
	display: block;
	background: #c43f57;
}
.affiliates {
	min-height: 200px;
	width: 100%;
	display: block;
	background: #e9e9e9;
}
.home-hero {
	width: 100%;
	min-height: 588px;
	display: block;
	background: #ccc;
	//background-image: url('../../../../uploads/2020/03/person-on-rocks.jpg');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 50% 50%;
}
.home-main {
	width: 100%;
	min-height: 620px;
	display: block;
	background: #e9e9e9;
}
.page-header {
	width: 100%;
	min-height: 150px;
	background: #840421;
	display: block;
}
.mainbody {
	background: #fff;
	display: block;
	min-height: 1000px;
}
.news-area {
	width: 100%;
	min-height: 420px;
	display: block;
	background: #fff;
	border-top: 3px solid #840421;
}
.home-mission {
  width: 100%;
  min-height: 500px;
  display: block;
  background: #840421;
  @include breakpoint (sm) {
  	background-image: url('../../../../uploads/2020/04/isolated.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: right;
  }
}
.int-body {
	width: 100%;
	min-height: 1000px;
	display: block;
	background: #e9e9e9;
}
.ha-footer {
	color: #fff;
	background: #c43f57;
	min-height: 200px;
	width: 100%;
	display: block;
	border-top: 4px solid #840421;
}
.footer-links ul, 
.footer-links li {
	margin: 0; 
	padding: 0;
	margin-left: 8px;
}
.no-padding {
	padding-left: 0px;
	padding-right: 0px;
}
.tag-area {
	background: #a03d51;
}

// Custom Buttons
.btn-outline-red1 {
  color: #e9e9e9;
  border-color: #e9e9e9;
  transition: 0.3s ease-in-out;

  &:hover {
    color: #e9e9e9;
    background-color: #c43f57;
  }
}
.btn-outline-red2 {
  color: #fff;
  border-color: #e9e9e9;
  background-color: #680413;
  transition: 0.3s ease-in-out;

  &:hover {
    color: #e9e9e9;
    background-color: #c43f57;
  }
}
.border-red1 {
    border-color: #bf0531;
}
